<template>
  <div v-if="youtubeKey" class="film-card">
    <div class="film-info">
      <h2>{{ filmDetail.title }}</h2>
      <div class="film-score">
        <span>Release date: {{ releaseDate }}</span>
      </div>
      <p>{{ filmDetail.overview }}</p>
      <button @click="sendData">Trailer</button>
    </div>
    <div class="poster">
      <img :src="this.imgSrc" alt="err loading img" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    filmId: null
  },
  data() {
    return {
      filmDetail: null,
      youtubeKey: null,
      imgSrc: null
    };
  },
  methods: {
    sendData() {
      this.$emit("showDetail", {
        filmDetail: this.filmDetail,
        youtubeKey: this.youtubeKey
      });
    }
  },
  computed: {
    releaseDate() {
      return this.filmDetail.release_date
        .split("-")
        .reverse()
        .join("-");
    }
  },
  created() {
    //get film detail
    fetch(
      `https://api.themoviedb.org/3/movie/${this.filmId}?api_key=e041ade24930d13fa4444a5b88f8b6c5&language=en`
    )
      .then(res => res.json())
      .then(result => {
        this.filmDetail = result;
        this.imgSrc = `https://image.tmdb.org/t/p/original/${result.poster_path}`;
      });
    //get film trailer
    fetch(
      `https://api.themoviedb.org/3/movie/${this.filmId}/videos?api_key=e041ade24930d13fa4444a5b88f8b6c5&language=en-US`
    )
      .then(res => res.json())
      .then(result => {
        if (result.results[0]) {
          this.youtubeKey = result.results[0].key;
        }
      });
  }
};
</script>

<style>
.film-info {
  width: 60%;
  line-height: 2rem;
}

.film-info h2 {
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 3rem;
  line-height: 3.5rem;
}

.film-card {
  width: auto;
  height: 100%;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  background: rgba(0, 0, 0, 0.62);
  box-shadow: 0 0 20px rgba(104, 48, 48, 0.55);
  border-radius: 3px;
}

.film-card button {
  margin-top: 50px;
}

.poster img {
  width: 100%;
  height: 450px;
  object-fit: contain;
}

.poster {
  width: 40%;
  order: 0;
  padding: 0 15px;
}
.film-card:nth-child(2n + 1) {
  flex-direction: row-reverse;
}
</style>
