<template>
  <div class="film-detail">
    <button @click="hideComp">X</button>
    <div class="trailer" v-if="youtubeKey">
      <iframe
        :src="'https://www.youtube.com/embed/' + youtubeKey"
        frameborder="0"
        allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
        allowfullscreen
      ></iframe>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activedFilm: null
  },
  data() {
    return {
      youtubeKey: this.activedFilm.youtubeKey,
      filmDetail: this.activedFilm.filmDetail
    };
  },
  methods: {
    hideComp() {
      this.$emit("hideDetail");
    }
  }
};
</script>

<style>
.film-detail {
  position: fixed;
  width: 70vw;
  height: 70vh;
  z-index: 100;
  top: 15vh;
  left: 15vw;
  background: rgba(0, 0, 0, 0.438);
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.film-detail button {
  margin-left: auto;
  position: relative;
}
.trailer {
  width: 100%;
  padding-top: calc(56.25%);
  position: relative;
  background: black;
}
.trailer iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
</style>
