<template>
  <div id="app">
    <div class="page-back"></div>
    <comp-header v-on:search="getData($event)"></comp-header>
    <comp-body v-bind:searchUrl="searchUrl"></comp-body>
  </div>
</template>

<script>
import compHeader from "./header-comp/Comp-Header";
import compBody from "./body-comp/Comp-body";

export default {
  components: {
    compHeader,
    compBody
  },
  props: {},
  name: "app",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      searchUrl: null
    };
  },
  methods: {
    getData(e) {
      this.searchUrl = e;
    }
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
  color: white;
  position: relative;
  z-index: 100;
}

.page-back {
  background: linear-gradient(
      0deg,
      rgb(0, 0, 0) 0%,
      rgba(51, 51, 51, 0.2) 49%,
      rgb(0, 0, 0) 100%
    ),
    url("./assets/avengers_infinity_war_4k_8k-1920x1200.jpg");
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
}

