<template>
  <div class="film-container">
    <div class="film-list">
      <film-card
        v-for="film in filmData"
        :key="film.id"
        :filmId="film.id"
        @showDetail="showDetail($event)"
      ></film-card>
    </div>
    <div class="page-control" v-if="filmData">
      <button v-on:click="goToPrevPage">Prev page</button>
      <button v-on:click="goToNextPage">next page</button>
      <span>{{ page }}/{{ pagesCounter }}</span>
    </div>
    <transition name="fade">
      <film-detail
        :activedFilm="activedfilm"
        v-if="activedfilm"
        @hideDetail="activedfilm = null"
      ></film-detail>
    </transition>
  </div>
</template>

<script>
import filmCard from "./Comp-Film-Card";
import filmDetail from "./Comp-Film-Detail";

export default {
  components: {
    filmCard,
    filmDetail
  },
  props: {
    searchUrl: null
  },
  data() {
    return {
      filmData: null,
      page: 1,
      pagesCounter: null,
      activedfilm: null
    };
  },
  methods: {
    goToNextPage() {
      if (this.page >= this.pagesCounter) {
        this.page = 1;
      } else {
        this.page++;
      }
    },
    goToPrevPage() {
      if (this.page > 1) {
        this.page--;
      }
    },
    showDetail(e) {
      this.activedfilm = e;
    }
  },
  computed: {},
  watch: {
    searchUrl: function(newData) {
      this.page = 1;
      let url = `https://api.themoviedb.org/3/search/movie?api_key=e041ade24930d13fa4444a5b88f8b6c5&language=en-US&query=${newData.searchText}&page=${this.page}&sort_by=popularity.desc`;
      fetch(url).then(res => {
        res.json().then(value => {
          this.filmData = value.results;
          this.pagesCounter = value.total_pages;
        });
      });
    },

    page: function(newData) {
      let url = `https://api.themoviedb.org/3/search/movie?api_key=e041ade24930d13fa4444a5b88f8b6c5&language=en-US&query=${this.searchUrl.searchText}&page=${newData}&sort_by=popularity.desc`;
      fetch(url).then(res => {
        res.json().then(value => {
          this.filmData = value.results;
          this.pagesCounter = value.total_pages;
        });
      });
    }
  }
};
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(100px);
}
.film-container {
  width: 100%;
  padding: 100px;
}
.film-list {
  margin-top: 100px;
  display: grid;
  grid-template-columns: auto;
  grid-gap: 20px;
  height: auto;
}
.film-container button {
  padding: 10px 30px;
  background: #e23c3c;
  border: none;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
  outline: none;
}
.film-container button:hover {
  background: #ee4040;
}
</style>
